@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    display: flex;
    flex-direction: column;
    &__fieldset {
        border: none;
        margin-top: 1rem;
        padding: 0;
    }
    &__legend {
        color: v.$gris;
        font-size: 2.2rem;
        margin-bottom: 2rem;
        margin-top: 2rem;
    }

    &__grid{
        @include m.tablet { 
            @include m.grid(4,1rem)
         }
    }

    &__campo {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin: 1.5rem 0;

        &--radio{
            @extend .formulario__campo;
            padding: 0 .5rem;
            @include m.tablet { 
              all: unset;
              @include m.grid(2, 5rem);
              align-items: center;
             }

        }

        &--boton-radio{
            display: flex;
            gap: 1rem;
            align-items: center;
            justify-content: space-between;
            @include m.tablet { 
              justify-content: unset;
             }

             
        }

        
    }
    &__contenedor-icono {
        display: flex;
        border-radius: .5rem;
        overflow: hidden;
    }
    &__icono {
        background-color: v.$gris-oscuro;
        width: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;

        i {
            color: v.$blanco;
            font-size: 2rem;
        }
    }
    &__label {
        display: block;
        font-size: 1.8rem;
        color: rgba(v.$negro, .8);
    }
    &__input,
    &__select {
        padding: 1rem;
        border: 1px solid darken(v.$gris-claro, 20);
        border-radius: .5rem;

        &--file {
            border: none;
        }
        &--sociales {
            @extend .formulario__input;
            flex: 1;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
        &--radio{
            
            cursor: pointer;
            
        }
    }

    &__radio{
        display: flex;
        gap: 2rem;
    }

    &__texto {
        margin-top: 3rem;
    }
    &__imagen {
        max-width: 20rem;
    }

    &__listado {
        @include m.tags;
    }

    &__submit {
        @include m.boton(v.$azul, v.$blanco);
        width: auto;
        align-self: flex-start;

        &--registrar,
        &--full {
            width: 100%;
        }
    }
}